import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { isEmpty, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Vinculo } from '../../../../../models/dtos/mfp/vinculo';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, SeguroDesempregoRV } from './seguroDesempregoRV';

const DEFAULT_VALUES: SeguroDesempregoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  vinculos: [],
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const SeguroDesempregoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<SeguroDesempregoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const validateExtras = () => {
    const seguroDesempregoRV: SeguroDesempregoRV = formProps.getValues();
    const erros = [];

    seguroDesempregoRV.vinculos.forEach((vinculo: Vinculo) => {
      if (isEmpty(vinculo.recisao)) {
        erros.push(`O Funcionário ${vinculo.matricula} - ${vinculo.trabalhador.nome} não possui rescisão cadastrada`);
      }
    });

    return erros;
  };

  return (
    <WindowComponent
      title="Seguro Desemprego"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
      wikiId={552}
      wikiTitle={'Arquivo Para Requerimento do Seguro Desemprego'}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <VinculoIS
          id="vinculos"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Funcionário(s)"
          placeholder="Selecione Funcionário(s)"
          isPrependLabel={true}
          inputSearchProps={{ selectProps: { isMulti: true } }}
        />
      </ReportForm>
    </WindowComponent>
  );
};

export default SeguroDesempregoReportView;
